"use client";

import { FaUsers, FaBoxOpen, FaChartLine } from "react-icons/fa";

export default function Overview() {
  return (
    <div>
      {/* Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        <div className="bg-white shadow rounded-lg p-6 flex items-center justify-between hover:shadow-xl transition-shadow">
          <div>
            <h3 className="text-gray-500">Total Users</h3>
            <p className="text-2xl font-bold text-gray-800">1,234</p>
          </div>
          <FaUsers className="text-3xl text-blue-500" />
        </div>

        <div className="bg-white shadow rounded-lg p-6 flex items-center justify-between hover:shadow-xl transition-shadow">
          <div>
            <h3 className="text-gray-500">Total Products</h3>
            <p className="text-2xl font-bold text-gray-800">567</p>
          </div>
          <FaBoxOpen className="text-3xl text-green-500" />
        </div>

        <div className="bg-white shadow rounded-lg p-6 flex items-center justify-between hover:shadow-xl transition-shadow">
          <div>
            <h3 className="text-gray-500">Monthly Sales</h3>
            <p className="text-2xl font-bold text-gray-800">$12,345</p>
          </div>
          <FaChartLine className="text-3xl text-purple-500" />
        </div>
      </div>

      {/* Table */}
      <div className="mt-8 bg-white shadow rounded-lg p-6 overflow-auto">
        <h3 className="text-lg font-semibold text-gray-700 mb-4">Recent Orders</h3>
        <table className="min-w-full table-auto">
          <thead>
            <tr className="bg-gray-50">
              <th className="px-4 py-2 text-left text-gray-600">Order ID</th>
              <th className="px-4 py-2 text-left text-gray-600">Customer</th>
              <th className="px-4 py-2 text-left text-gray-600">Amount</th>
              <th className="px-4 py-2 text-left text-gray-600">Status</th>
            </tr>
          </thead>
          <tbody>
            <tr className="border-t hover:bg-gray-50 transition">
              <td className="px-4 py-2">#1234</td>
              <td className="px-4 py-2">John Doe</td>
              <td className="px-4 py-2">$234</td>
              <td className="px-4 py-2 text-green-500 font-semibold">Completed</td>
            </tr>
            <tr className="border-t hover:bg-gray-50 transition">
              <td className="px-4 py-2">#1235</td>
              <td className="px-4 py-2">Jane Smith</td>
              <td className="px-4 py-2">$120</td>
              <td className="px-4 py-2 text-yellow-500 font-semibold">Pending</td>
            </tr>
            <tr className="border-t hover:bg-gray-50 transition">
              <td className="px-4 py-2">#1236</td>
              <td className="px-4 py-2">Bob Johnson</td>
              <td className="px-4 py-2">$560</td>
              <td className="px-4 py-2 text-red-500 font-semibold">Cancelled</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}
